.headFooter_content_text{
    display: flex;
    flex-direction: row;
    background-color: rgb(40, 39, 39);
    color: white;
    letter-spacing: 1px;
}
.headFooter_content_size{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 50%;
}
.headFooter_content_size_dl{
    border-left: 1px solid white;
    padding-left: 1rem; 
}
.headFooter_content_size dd{
    margin: 0;
    font-size: 13px;
}
.headFooter_content_size dt{
    margin: 1rem 0;
    color: gray;
    font-size: 16px;
    font-weight: bold;
}
.headFooter_content_size dl{
    margin-left: 1rem;
}
@media (max-width: 658px) {
    .headFooter_content_text{
        display: flex;
        flex-direction: column;
    }
    .headFooter_content_size{
        width: 100%;
        margin: 0.5rem 0rem;
    }
    .headFooter_content_size_dl{
        border-left: none;
        padding: 0; 
    }
    .headFooter_content_size dl{
        margin: 0rem 0.5rem;
    }
}