.purchaseFooter_content{
    display: flex;
    flex-direction: row;
    background-color: rgb(40, 39, 39);
    color: white;
    border-top: 1px solid white;
    padding: 1rem;
}
.purchaseFooter_content_size{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 50%;
    
}
.purchaseFooter_content_size button{
    background-color: transparent;
    border: none;
    color: white;
    font-size: 15px;
    font-weight: bold;
    cursor: pointer;
}
.purchaseFooter_content_size button:hover{
    background-color: gray;
}
.purchaseFooter_content_size_button button:hover{
    background-color: transparent;
}
.purchaseFooter_content_size_button{
    display: flex;
    width: 100%;
}
.purchaseFooter_content_size_button button{
    width: 100%;
    
}
.purchaseFooter_content_size_button img{
    width: 100%;
}   
.purchaseFooter_content_size span{
    margin: 0rem 0.25rem;
}
@media (max-width: 658px) {
    .purchaseFooter_content{
        display: flex;
        flex-direction: column;
    }
    .purchaseFooter_content_size{
        width: 100%;
        margin: 0.5rem 0rem;
    }
}